




































import Vue from 'vue';
import { User } from '@/store/modules/user/types';
import * as utils from '@/utils';

export default Vue.extend({
  name: 'PlayerInfo',
  computed: {
    gameflag(): string {
      return this.$store.state.profile.user.sGameFlag;
    },
    user(): User | undefined {
      const sid = this.$store.state.session.currentSessionId;
      if (sid && this.$store.state.user.users[sid]) {
        const user: User = this.$store.state.user.users[sid];
        return user;
      }
      return undefined;
    },
    isOnline(): boolean {
      if (this.user) {
        return this.user.iOnline === 1;
      }
      return true;
    },
    playerName(): string {
      if (this.user) {
        return `${this.user.sName} ID:${this.user.pid}`;
      }
      return '';
    },
    description(): string {
      let sDesc = '';
      if (this.user) {
        const u = this.user;
        sDesc = `:${u.sPlatform}_:${u.iServer}_:${u.iGrade}_:${u.iMoneyMax}`;
      }
      return sDesc;
    },
  },
  data() {
    return {
      copyBtn: null, // 
    };
  },
  methods: {
    onCopy() {
      this.$message({ message: 'ID', duration: 700 });
    },
  },
  mounted() {
    const Clipboard = (this as any).clipboard;
    this.copyBtn = new Clipboard(this.$refs.copy);
  },
});
